import React, { useState, useEffect } from 'react';
import styled, { keyframes, ThemeProvider } from 'styled-components';
import { DarkTheme } from './Themes';
import PowerButton from '../subComponents/PowerButton';
import LogoComponent from '../subComponents/LogoComponents';
import SocialIcon from '../subComponents/SocialIcon';
import ParticleComponent from '../subComponents/ParticleComponent';
import astronaut from "../assets/Images/spaceman.webp";
import BigTitle from '../subComponents/BigTitle';
import libraryData from '../data/library';

const float = keyframes`
  0% { transform: translateY(-10px); }
  50% { transform: translateY(30px) translateX(30px); }
  100% { transform: translateY(-10px); }
`;

const Box = styled.div`
  background-color: ${(props) => props.theme.body};
  width: 100vw;
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
  font-family: 'Orbitron', monospace;
`;

const Spaceman = styled.div`
  position: absolute;
  top: 10%;
  right: 10%;
  width: 25vw;
  animation: ${float} 5s ease-in-out infinite;
  z-index: 1;
  will-change: transform;

  img {
    width: 100%;
    height: auto;
  }

  @media (max-width: 768px) {
    width: 35vw;
  }
`;

const CardWrapper = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  gap: 2rem;
  z-index: 2;

  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    top: 15%;
    bottom: 2rem;
    transform: translateX(-50%);
  }

  @media (max-width: 480px) {
    gap: 1rem;
  }
`;

const Card = styled.div`
  border: 2px solid ${(props) => props.theme.text};
  color: ${(props) => props.theme.text};
  padding: 1.5rem;
  width: 40vw;
  max-height: 70vh;
  backdrop-filter: blur(4px);
  font-size: 1rem;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  overflow-y: auto;

  @media (max-width: 1024px) {
    width: 80vw;
  }

  @media (max-width: 768px) {
    width: 68vw;
    padding: 1rem;
    font-size: 0.8rem;
  }

  @media (max-width: 480px) {
    width: 90vw;
    padding: 1rem;
    font-size: 0.9rem;
  }
`;

const EntryBlock = styled.pre`
  white-space: pre-wrap;
  background-color: rgba(255,255,255,0.07);
  padding: 1rem;
  border-radius: 6px;
  font-family: 'Courier New', monospace;
  font-size: 1rem;
  color: #ccc;
  margin-bottom: 1.5rem;
`;

const AboutPage = () => {
  const [entries, setEntries] = useState([]);

  useEffect(() => {
    setEntries(libraryData);
  }, []);

  return (
    <ThemeProvider theme={DarkTheme}>
      <Box>
        <LogoComponent theme="dark" />
        <SocialIcon theme="dark" />
        <PowerButton theme="dark" />
        <ParticleComponent theme="dark" />
        <Spaceman>
          <img src={astronaut} alt="spaceman" />
        </Spaceman>

        <CardWrapper>
          <Card>
            <h3>📘 Notepad</h3>
            {entries.length === 0 ? (
              <p>Tidak ada data ditemukan.</p>
            ) : (
              entries.map((entry, index) => (
                <EntryBlock key={index}>
{`# Entry ${index + 1}
Judul: ${entry.title}
Deskripsi:
${entry.desc.map(line => `- ${line}`).join("\n")}`}
                </EntryBlock>
              ))
            )}
          </Card>
        </CardWrapper>

        <BigTitle text="Diary Work FM+" top="2%" left="4%" />
      </Box>
    </ThemeProvider>
  );
};

export default AboutPage;
